<template>
  <div class="course-search">
    <el-form label-position="left" label-width="80px" :model="course" inline>
      <el-form-item label="课程名称" class="search-item">
        <el-input
          v-model="course.name"
          clearable
          placeholder="请输入课程名称"
        />
      </el-form-item>
      <el-form-item label="创建时间" class="search-item">
        <el-date-picker
          v-model="course.time"
          type="daterange"
          align="right"
          unlink-panels
          clearable
          range-separator="-"
          start-placeholder="创建时间"
          end-placeholder="创建时间"
          value-format="yyyy-MM-dd"
        />
      </el-form-item>
      <el-form-item label="课程类别" class="search-item">
        <el-select v-model="course.category" clearable placeholder="请选择">
          <el-option
            v-for="item in courseCategory"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="课程类型" class="search-item">
        <el-select v-model="course.type" clearable placeholder="请选择">
          <el-option
            v-for="item in courseType"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" class="select-btn" @click="onSubmit"
          >查询</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      course: {
        name: '',
        time: ['', ''],
        category: 'all',
        type: 'all',
        isSearch: true
      },
      courseCategory: [
        { value: 'all', label: '全部' },
        { value: 'image_text', label: '图文' },
        { value: 'video', label: '视频' },
        { value: 'audio', label: '音频' }
      ],
      courseType: [
        { value: 'all', label: '全部' },
        { value: 'series_lesson', label: '系列课' },
        { value: 'single_lesson', label: '单节课' }
      ]
    }
  },
  watch: {
    course: {
      handler() {
        this.$emit('getSearch', this.course)
      },
      deep: true
    }
  },
  methods: {
    onSubmit() {
      this.$emit('getCourseList', this.course)
    }
  }
}
</script>

<style lang="scss" scoped>
.course-search {
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;
  margin-bottom: 20px;
  /deep/form {
    width: 100%;
    .el-date-editor {
      padding: 0 10px;
      width: 220px;
      height: 32px;
      .el-range-input {
        width: 85px;
        height: 30px;
      }
    }
  }
  .search-item {
    margin-right: 70px;
    color: #17233d;
    /deep/input {
      width: 220px;
      height: 32px;
    }
    /deep/span {
      top: -3px;
    }
    /deep/.el-input__suffix {
      top: 0;
    }
  }
  .select-btn {
    padding: 0 20px;
    width: 68px;
    height: 32px;
  }
  .arrow {
    float: right;
  }
}
</style>
