<template>
  <div class="course-list-info">
    <div class="title">
      <div>课程列表</div>
      <el-dropdown>
        <el-button type="primary">
          添加课程<i class="el-icon-arrow-down el-icon--right" />
        </el-button>
        <el-dropdown-menu
          slot="dropdown"
          style="width: 117px;text-align: center;"
        >
          <el-dropdown-item
            ><a href="/learning-admin/course/series-course"
              >系列课</a
            ></el-dropdown-item
          >
          <el-dropdown-item
            ><a href="/learning-admin/course/single"
              >单节课</a
            ></el-dropdown-item
          >
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <el-table :data="list" style="width: 100%" :empty-text="hasListInfo">
      <el-table-column
        fixed
        prop="num"
        label="序号"
        min-width="120"
        align="center"
      />
      <el-table-column prop="id" label="编号" min-width="150" align="center">
        <template slot-scope="scope">
          <el-popover
            placement="top-start"
            trigger="hover"
            :content="scope.row.id"
          >
            <span slot="reference" class="cell" v-html="scope.row.id" />
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column
        prop="name"
        label="课程名称"
        min-width="120"
        align="center"
      >
        <template slot-scope="scope">
          <el-popover
            placement="top-start"
            trigger="hover"
            :content="scope.row.name"
          >
            <span slot="reference" class="cell" v-html="scope.row.name" />
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column
        prop="image"
        label="课程封面"
        min-width="120"
        align="center"
      >
        <template slot-scope="scope">
          <el-avatar :size="30" :src="scope.row.image" />
        </template>
      </el-table-column>
      <el-table-column
        prop="courseNum"
        label="课程节数"
        min-width="120"
        align="center"
      />
      <el-table-column
        prop="uploadNum"
        label="上传节数"
        min-width="120"
        align="center"
      />
      <el-table-column
        prop="courseCategory"
        label="课程类别"
        min-width="120"
        align="center"
      />
      <el-table-column align="center" label="创建日期" min-width="150">
        <template slot-scope="scope">
          {{ scope.row.created_at | formatTime('{y}-{m}-{d}') }}
        </template>
      </el-table-column>
      <el-table-column
        prop="courseType"
        label="课程类型"
        min-width="120"
        align="center"
      />
      <el-table-column fixed="right" label="操作" width="150" align="center">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="isDelShow(scope.row)"
            >删除</el-button
          >
          <el-button
            v-if="scope.row.category === 'series_lesson'"
            type="text"
            size="small"
            @click="editCourse(scope.row, 'edit')"
            >编辑</el-button
          >
          <el-button type="text" size="small" @click="editCourse(scope.row)">{{
            scope.row.category === 'series_lesson' ? '去上传' : '编辑'
          }}</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        :current-page="currentPage"
        :page-sizes="[12, 18, 24]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>
    <el-dialog
      title="删除提示"
      :visible.sync="delShow"
      width="30%"
      center
      top="30vh"
    >
      <div class="center">
        <p>删除后，该课程下所有信息将不再保留，</p>
        <p>请确定是否删除？</p>
        <el-input
          v-model="inputDel"
          placeholder="请在输入框输入“删除”"
          class="del-input"
          @input="setDelStatus"
        />
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button
          @click="
            delShow = false
            inputDel = ''
          "
          >取 消</el-button
        >
        <el-button :type="delType" :disabled="disabledDel" @click="confirm"
          >确 定</el-button
        >
      </span>
    </el-dialog>
    <el-dialog
      title="删除提示"
      :visible.sync="delSingleShow"
      width="30%"
      center
      top="30vh"
    >
      <div class="center">
        <p>删除后，该课程下所有信息将不再保留，</p>
        <p>请确定是否删除？</p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button
          @click="
            delSingleShow = false
            inputDel = ''
          "
          >取 消</el-button
        >
        <el-button type="primary" @click="confirm">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getCourseList, delCourse } from '@/api/learningAdmin'
export default {
  props: {
    searchInfo: {
      type: [Object, String],
      default: ''
    }
  },
  data() {
    return {
      list: [],
      currentPage: 1,
      delShow: false,
      pageSize: 12,
      count: 0,
      rowInfo: '',
      hasListInfo: '暂无数据',
      inputDel: '',
      disabledDel: true,
      delSingleShow: false,
      delType: 'info'
    }
  },
  computed: {
    getType() {
      return function(type) {
        type = type ? type.split('|') : []
        const showType = []
        if (type.indexOf('video') >= 0) showType.push('视频')
        if (type.indexOf('audio') >= 0) showType.push('音频')
        if (type.indexOf('image_text') >= 0) showType.push('图文')
        if (!type.length) showType.push('未知')
        return showType.join(',')
      }
    }
  },
  created() {
    this.getCourseList()
  },
  methods: {
    async confirm() {
      const { data } = await delCourse(this.rowInfo.id)
      if (data) {
        if (data.result) {
          this.$message({ message: '删除成功', type: 'success' })
          this.getCourseList()
          this.delShow = false
          this.delSingleShow = false
          this.inputDel = ''
        } else this.$message({ message: data.message, type: 'error' })
      }
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getCourseList(this.searchInfo)
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getCourseList(this.searchInfo)
    },
    async getCourseList(search) {
      let params
      if (search) {
        if (search.isSearch) this.currentPage = 1
        params = {
          started_at: search.time ? search.time[0] : '',
          ended_at: search.time ? search.time[1] : '',
          title: search.name || undefined,
          category: search.type || undefined,
          type: search.category || undefined,
          page: this.currentPage,
          pageSize: this.pageSize
        }
      } else {
        params = {
          page: this.currentPage,
          pageSize: this.pageSize
        }
      }
      const { data, code } = await getCourseList(params)
      if (code === 20000 && data.list.length) {
        this.count = data.count
        this.list = data.list.map((item, index) => {
          return {
            num: (data.page - 1) * data.pageSize + index + 1,
            id: item._id,
            name: item.title,
            image: item.cover,
            courseNum: item.total_chapter || 1,
            uploadNum:
              item.category === 'series_lesson' ? item.available_chapter : 1,
            category: item.category,
            lesson_column: item.lesson_column,
            courseCategory: this.getType(item.type),
            created_at: item.created_at,
            courseType: item.category === 'single_lesson' ? '单节课' : '系列课'
          }
        })
      } else this.list = []
      if (this.list.length === 0) {
        this.hasListInfo = '暂无数据，请重新输入关键词'
      }
    },
    setDelStatus() {
      if (this.inputDel === '删除') {
        this.disabledDel = false
        this.delType = 'primary'
      } else {
        this.disabledDel = true
        this.delType = 'info'
      }
    },
    editCourse(row, type) {
      if (row.category === 'series_lesson' && type === 'edit') {
        this.$router.push(`/learning-admin/course/series-edit/${row.id}`)
      } else if (row.category === 'single_lesson') {
        this.$router.push(`/learning-admin/course/single-edit/${row.id}`)
      } else {
        this.$router.push(`/learning-admin/course/series-detail/${row.id}`)
      }
    },
    isDelShow(row) {
      if (row.category === 'series_lesson') this.delShow = true
      else this.delSingleShow = true
      this.rowInfo = row
    }
  }
}
</script>

<style lang="scss" scoped type="text/scss">
.course-list-info {
  margin: 20px 0;
  padding: 20px;
  border: 1px solid #ebeef5;
  .title {
    display: flex;
    justify-content: space-between;
    padding-bottom: 30px;
    line-height: 32px;
    font-size: 20px;
    font-weight: bold;
    color: #333;
    /deep/button {
      padding: 6px 20px;
      height: 32px;
    }
  }
  /deep/.el-dialog__wrapper {
    .el-dialog__headerbtn {
      top: 10px;
      right: 5px;
    }
  }
  .pagination {
    margin-top: 50px;
    text-align: center;
  }
  /deep/.cell .el-button {
    padding: 0;
  }
  /deep/.del-input {
    margin-top: 15px;
    input {
      width: 220px;
    }
  }
  /deep/.el-table .cell {
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
    *:not(button) {
      display: inline-block;
      width: 100%;
    }
  }
  .center {
    text-align: center;
    p {
      margin-bottom: 10px;
    }
  }
}
</style>
