<template>
  <div class="course-list">
    <search @getCourseList="getCourseList" @getSearch="getSearch" />
    <course-list ref="courseList" :search-info="searchInfo" />
  </div>
</template>

<script>
import Search from './components/Search'
import CourseList from './components/CourseListInfo'
export default {
  components: {
    Search,
    CourseList
  },
  data() {
    return {
      searchInfo: ''
    }
  },
  methods: {
    getCourseList(search) {
      this.$refs.courseList.getCourseList(search)
    },
    getSearch(search) {
      this.searchInfo = search
    }
  }
}
</script>
